import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import Icon from '@mui/material/Icon';
import Tooltip from '@mui/material/Tooltip';

import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import RadioButtonUncheckedIcon from '@mui/icons-material/RadioButtonUnchecked';
import CancelIcon from '@mui/icons-material/Cancel';
import CheckIcon from '@mui/icons-material/Check';
import DeleteIcon from '@mui/icons-material/Delete';
import {
  CardWrapper,
  ProductName,
  Wrapper,
  Status,
  Wrap,
  WrapArrow,
  Dates,
  Pag,
  Weight,
  ButtonWrapper,
} from './ShipmentBlock.styled';
import {
  StyledIconRadioButton,
  StyledIconCancelButton,
  StyledIconCheckButton,
  StyledIconDeleteButton,
} from './ShipmentBlockIcon.styled';
import { isInternational } from 'fakeApi';

const getStatusColor = status => {
  let className = 'default';

  switch (status) {
    case ' ':
      className = 'in-progress';
      break;
    case '':
      className = 'completed';
      break;
    default:
      className = 'default';
      break;
  }

  return className;
};

export const ShipmentBlock = ({
  shipment,
  condition,
  handleStatusChange,
  handleDelete,
}) => {
  const location = useLocation();

  return (
    <CardWrapper key={shipment.id} className={getStatusColor(shipment.status)}>
      <Link
        to={`${shipment.id}`}
        state={{ from: location }}
        onClick={!condition ? e => e.preventDefault() : undefined}
        key={shipment.id}
        shipment={shipment}
        condition={true}
        handleStatusChange={handleStatusChange}
        handleDelete={handleDelete}
      >
        <ProductName>
          {isInternational(shipment)
            ? ' '
            : ' '}{' '}
          : {shipment.shipmentNumber}
          <Weight>: {shipment.weight} </Weight>
          <Dates>: {new Date(shipment.date).toLocaleDateString()}</Dates>
        </ProductName>
        <Status>: {shipment.status}</Status>
        <Wrapper>
          <Wrap>
            <Pag> :</Pag>
            <Pag>
              {shipment.origin.city}, {shipment.origin.country}
            </Pag>
          </Wrap>
          <WrapArrow>
            <Icon component={ArrowForwardIcon} />
          </WrapArrow>
          <Wrap>
            <Pag> :</Pag>
            <Pag>
              {shipment.destination.city}, {shipment.destination.country}
            </Pag>
          </Wrap>
        </Wrapper>
      </Link>

      {condition && (
        <ButtonWrapper>
          {shipment.status !== ' ' && (
            <Tooltip title="  ''" placement="left">
              <StyledIconRadioButton
                onClick={() => handleStatusChange(shipment.id, ' ')}
                style={{
                  width: '24px',
                  height: '24px',
                }}
              >
                <RadioButtonUncheckedIcon />
              </StyledIconRadioButton>
            </Tooltip>
          )}
          {shipment.status !== '' && (
            <Tooltip title="  ''" placement="left">
              <StyledIconCancelButton
                onClick={() => handleStatusChange(shipment.id, '')}
                style={{ width: '24px', height: '24px' }}
              >
                <CancelIcon />
              </StyledIconCancelButton>
            </Tooltip>
          )}
          {shipment.status !== '' && (
            <Tooltip title="  ''" placement="left">
              <StyledIconCheckButton
                onClick={() => handleStatusChange(shipment.id, '')}
                disabled={shipment.status === ''}
                style={{ width: '24px', height: '24px' }}
              >
                <CheckIcon />
              </StyledIconCheckButton>
            </Tooltip>
          )}
          <Tooltip title=" " placement="left">
            <StyledIconDeleteButton
              onClick={() => handleDelete(shipment.id)}
              style={{ width: '24px', height: '24px' }}
            >
              <DeleteIcon />
            </StyledIconDeleteButton>
          </Tooltip>
        </ButtonWrapper>
      )}
    </CardWrapper>
  );
};
